<template>
  <div class="center">
    <h3 v-html="'来粉统计 / 帮助文档 / 帮助详情'"></h3>
    <p class="title">{{result.title}}</p>
    <p class="time">{{result.create_time}}</p>
    <div class="content">{{result.content}}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        id: "",
        result: {}
  }
  },
  mounted(){
      var params = this.$route.params;
      this.id = params.id;
      this.getData();
      let des = document.querySelectorAll("meta[name=description]")[0];
      let titleNode = document.querySelectorAll("title")[0];
      des.content = params.title + des.content;
      titleNode.innerText = params.title + titleNode.innerText;
  },
  methods: {
      getData(){
          this.axios.get("/v1/common/Helper/detail/id/" + this.id).then(res => {
              this.result = res.data.result
          })
      }
  },
  beforeDestroy(){
          let des = document.querySelectorAll("meta[name=description]")[0];
      let titleNode = document.querySelectorAll("title")[0];
      des.content = "【来粉统计】是基于推广微信号的营销模式的统计分析工具，基于百度竞价、360等落地页和复制、添加微信号的业务逻辑，打通整个数据环节，让转化数据清晰可见！";
      titleNode.innerText = "来粉统计 - 好用的微信号复制转化统计工具";
  }
};
</script>

<style lang="less" scoped>
h3 {
  color: #666;
  border-bottom: solid 1px #aaa;
  line-height: 260%;
  margin-top: 30px;
}
.title{
    margin-top: 50px;
}
.title, .time{
    font-size: 16px;
    text-align: center;
    line-height: 180%;
}
.content{
    font-size: 14px;
    margin-top: 30px;
    margin-bottom: 100px;
}
</style>