<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>来粉统计 / 控制台</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="data">
      <div class="left">
        <h3>今日数据</h3>
        <p>
          访客数（UV）
          <span>{{data.today_data.uv_num}}</span>
        </p>
        <p>
          IP数
          <span>{{data.today_data.ip_num}}</span>
        </p>
        <p>
          浏览量（PV）
          <span>{{data.today_data.pv_num}}</span>
        </p>
        <p>
          平均访问时长
          <span>{{data.today_data.avg_time}}</span>
        </p>
      </div>
      <div class="right">
        <h3>昨日数据</h3>
        <p>
          访客数（UV）
          <span>{{data.yesterday_data.uv_num}}</span>
        </p>
        <p>
          IP数
          <span>{{data.yesterday_data.ip_num}}</span>
        </p>
        <p>
          浏览量（PV）
          <span>{{data.yesterday_data.pv_num}}</span>
        </p>
        <p>
          平均访问时长
          <span>{{data.yesterday_data.avg_time}}</span>
        </p>
      </div>
    </div>
    <div class="hot">
      <p class="title">
        热门推荐
        <span></span>
      </p>
      <div class="hotMain">
        <div v-for="(e, i) of hotImg" :key="i" :src="e">
          <img :src="e" alt />
        </div>
        
      </div>
    </div>
    <div class="notice">
      <p class="title">
        公告
        <span></span>
      </p>
      <ul class="list">
        <li @click="detailDialog(e)" v-for="(e, i) of data.notice_info" :key="i">{{i+1}}. {{e.content}}<span style="float: right">{{e.create_time}}</span></li>
      </ul>
    </div>

<el-dialog :visible.sync="dialogVisible" title="公告" width="50%" :before-close="close" :close-on-click-modal = "false">
  <div>{{noticeDetail}}</div>
</el-dialog>

  </div>
</template>


<script>
import img1 from "../assets/Hot1.png";
import img2 from "../assets/Hot2.png";
import img3 from "../assets/Hot3.png";
export default {
  data() {
    return {
      hotImg: [img1, img2, img3],
      
        noticeDetail: "",
        dialogVisible: false,
      data: {
        today_data: {},
        yesterday_data: {},
        notice_info: []
      },
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    close(){
      this.dialogVisible = false;
      this.noticeDetail = ""
    },
    detailDialog(e){
      this.dialogVisible = true;
      this.noticeDetail = e.content;
    },
    getData() {
      this.axios.get("/v1/data/Home/index").then((res) => {
        if (res.data.code == 200) {
          this.data = res.data.result;
        }
      });
    }
  },
};
</script>

<style lang="less" scoped>
p.title {
  margin: 50px 0;
}
.data {
  overflow: hidden;
  p {
    line-height: 180%;
  }
  h3 {
    margin-bottom: 20px;
  }
  & > div {
    float: left;
    width: 30%;
    &:first-child{
        margin-right: 10%;
    }
    span{
        float: right;
    }
  }
}
.notice{
    li{
        line-height: 260%;
        cursor: pointer;
    }
}
.title {
  display: flex;
  span {
    flex: 1;
    border-bottom: dashed 1px #666;
    margin: 0 10px;
    height: 11px;
  }
}
.hot {
  .hotMain {
    display: flex;
    justify-content: space-around;
    & > * {
      width: 30%;
      img{
        width: 100%;
      }
    }
  }
}
</style>