<template>
  <div class="center">
    <h3>来粉统计 / 帮助文档</h3>
    <ul class="listBox">
      <li v-for="(e, i) of list" :key="i" @click="openDetails(e)">{{i+1}}. {{e.content}}<span class="fl-r">{{e.create_time}}</span></li>
    </ul>
    <el-pagination
      :page-size="page_size"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      page_size: 10,
      page: 1,
      total: 0,
      list: []
    };
  },
  mounted(){
      this.getData()
  },
  methods: {
    currentChange(e) {
      this.page = e;
      this.getData()
    },
    openDetails(e){
        this.$router.push({name: "helpdetail", params: e})
        
    },
    getData(){
        this.axios.get("/v1/common/Helper/list/page/" + this.page + "/page_size/" + this.page_size).then(res => {
            if(res.data.code == 200){
                this.total = res.data.result.page_info.total;
                this.list = res.data.result.list;
            }
        })
    }
  },
};
</script>

<style lang="less" scoped>
.listBox{
    margin-top: 50px;
    margin-bottom:60px;
}
h3 {
  color: #666;
  border-bottom: solid 1px #aaa;
  line-height: 260%;
  margin-top: 30px;
}
li{
    margin: 26px 0;
    cursor: pointer;
    &:hover{
        color: #409EFF
    }
}
</style>