<template>
  <div class="headera">
    <div class="wrapperCenter">
      <a href="/home">
        <img class="logo" style="margin-top:15px;" src="@/assets/logo.png" alt="来粉统计" />
      </a>
      <nav>
        <div v-for="(e, i) of nav" :key="i">
          <span v-if="e.child">
            {{ e.text }}
            <div v-if="e.child" class="childN">
              <router-link
                v-for="(item, index) of e.child"
                :key="index"
                :to="item.path"
                >{{ item.text }}</router-link
              >
            </div>
          </span>

          <router-link v-else :to="e.path">{{ e.text }}</router-link>
        </div>
      </nav>
      <div class="right">
        <div class="invitation" @click="invitation">
          <img src="@/assets/invitation.png" alt />
        </div>

        <div class="problem" @click="toHelp">帮助文档</div>
        <div class="order" @click="toOrder">工单</div>
        <div class="user">
          <div class="userBox">
            <div class="userInf">
              <img src="@/assets/user.png" alt />
              <div class="userMain">
                <p class="userName">{{ userInfo.user_name }}</p>
                <p class="userAccount">{{ userInfo.mobile }}</p>
              </div>
            </div>
            <ul class="set">
              <li @click="set">
                <span class="iconfont iconshezhiziduan"></span>
                <span>个人设置</span>
              </li>
              <li @click="logout">
                <span class="iconfont icontuichu5"></span>
                <span>退出</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      title="邀请朋友也加入"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      close="invitionBox"
    >
      <el-input type="text" v-model="copyUrl" disabled id="codeJS" />
      <p style="line-height: 280%;font-size:12px;margin-top:15px;">把链接复制给朋友即可完成邀请</p>
      <span class="dialog-footer">
        <el-button type="primary" @click="copy">复制</el-button>
      </span>
    </el-dialog>
  </div>
</template>


<script>
import { Loading } from "element-ui";
export default {
  data() {
    return {
      dialogVisible: false,
      nav: [
        {
          text: "控制台",
          path: "console",
        },
        {
          text: "项目设置",
          child: [
            {
              text: "域名绑定",
              path: "domain",
            },
            {
              text: "网址构建器",
              path: "website",
            },
          ],
        },
        {
          text: "流量分析",
          child: [
            {
              text: "实时访客",
              path: "visitor",
            },
          ],
        },
        {
          text: "转化分析",
          child: [
            {
              text: "页面分析",
              path: "page",
            },
            {
              text: "渠道分析",
              path: "channel",
            },
            {
              text: "地域分析",
              path: "geographical",
            },
          ],
        },
      ],
      userInfo: {},
      copyUrl: "",
    };
  },
  created() {
    this.userInfo = JSON.parse(localStorage.getItem("userInfo")).account_info;
  },
  methods: {
    handleClose() {
      this.copyUrl = "";
      this.dialogVisible = false;
    },
    copy() {
      var codeJS = document.getElementById("codeJS");
      codeJS.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message.success("复制成功");
      this.dialogVisible = false;
    },
    logout() {
      localStorage.removeItem("userInfo");
      this.$router.push("/home");
    },
    toHelp() {
      this.$router.push("/home/help");
    },
    invitation() {
      this.dialogVisible = true;
      const userId = JSON.parse(localStorage.getItem("userInfo")).account_info
        .id;
      this.copyUrl = "http://yaolaifen.com/?id=" + userId;
      // document.getElementsByClassName("invitionBox")[0].getElementsByClassName("el-loading-mask")[0].style.display = "none";
    },
    set() {
      this.$router.push("set");
    },
    toOrder() {
      this.$router.push("order");
    },
  },
};
</script>
<style lang="less">
.headera {
  .el-dialog {
    * {
      line-height: 100%;
    }
  }
}
</style>
<style lang="less" scoped>
@import url("//at.alicdn.com/t/font_1471400_8bliz6w96m2.css");
.hoverA{
  .childN{
    // display: block;
  }
}
.wrapperCenter {
  height: 100%;
}
.user {
  position: relative;
  margin-left: 0 !important;
  margin-right: 0 !important;
  cursor: pointer;

  .set {
    position: absolute;
    background: white;
    box-shadow: 0 2px 6px 1px #ccc;
    border-radius: 6px;
    padding: 16px 12px;
    top: 70px;
    left: 50%;
    margin-left: 20px;
    transform: translateX(40%);
    opacity: 0;
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
    .iconfont {
      color: #999;
      margin: 0 10px;
    }
    .iconshezhiziduan {
      color: #000;
    }
    li {
      width: 160px;
      clear: both;
      line-height: 44px;
      cursor: pointer;
      & > * {
        display: block;
        float: left;
      }
      img {
        width: 22px;
      }
      span {
        white-space: normal;
      }
    }
  }
}
.userMain {
  float: right;
  p {
    line-height: 26px;
    &:first-child {
      margin-top: 12px;
    }
  }
}
.userBox {
  height: 80px;
  &:hover .set {
    opacity: 1;
    transform: translateX(-50%);
  }
  .userInf {
    overflow: hidden;

    & > img {
      margin-top: 20px;
      display: block;
      float: left;
    }
  }
}
.problem,
.order,
.user {
  margin: 0 20px;
}
.headera {
  height: 80px;
  box-shadow: 0 0px 10px 1px #eaeaea;
  line-height: 80px;
  position: relative;
  z-index: 111;
  .wrapperCenter {
    .right {
      float: right;
      font-size: 12px;
      & > * {
        float: left;
      }
    }
    & > * {
      float: left;
    }
    .invitation {
      width: 100px;
      margin-top: 15px;
      cursor: pointer;
      img {
        width: 100%;
        float: left;
      }
    }
  }
}
nav {
  margin-left: 5%;
  height: 100%;
  margin-right: 5%;
  font-weight: 600;
  & > div {
    margin: 0 20px;
    position: relative;
    display: block;
    float: left;
    height: 100%;
    // &:hover .childN {
    //   opacity: 1;
    //   top: 70px;
    // }
    & > span,
    & > a {
      cursor: pointer;
      display: block;
      height: 46px;
      padding: 0 20px;
      margin-top: 20px;
      line-height: 46px;
      &:hover {
        background: #f4fcff;
        border-radius: 32px;
        color: #169bd5;
      }
      &:hover .childN {
        display: block;
      }
    }
  }
  .childN {
    display: none;
    position: absolute;
    top: 60px;
    left: 50%;
    border: solid 1px #ccc;
    border-radius: 5px;
    padding: 10px 10px;
    box-shadow: 0 2px 6px 1px #ccc;
    background: white;
    transition: opacity 0.3s, top 0.3s;

    transform: translateX(-50%);
    a {
      display: block;
      text-align: center;
      line-height: 32px;
      margin: 6px 0;
      white-space: nowrap;
      padding: 0 20px;
      &:hover {
        background: #409eff;
        color: white;
      }
    }
  }
  .action {
    background: #f4fcff;
    border-radius: 32px;
    color: #169bd5;
  }
}
.problem,
.order {
  cursor: pointer;
}
</style>